import React, { useState } from 'react';
import Input from '../Input';
import Checkbox from '../Checkbox';
import {
  Head,
  SelectBase,
  Fabric,
  Label,
  FormGroup,
  Result,
  Cost,
  Calculation,
  RadioGroup,
  RadioLabel,
  RadioBtn,
  Button,
} from '../Shared';
import { romeOptions } from '../../config';
import OrderModal from '../OrderModal';

const CORNICE_PRICE = 2200; //  
const TAPE_PRICE = 100; //     
const TAPE_COEF = 0.3; //    

const options = romeOptions.map(i => i.price);

const baseMap = romeOptions.reduce((res, i) => {
  res[i.price] = i.title;
  return res;
}, {});

const Rome = ({ option }) => {
  const [showOrderModal, toggleModal] = useState(false);

  const [values, setValues] = useState({
    base: options[option],
    width: null,
    height: null,
    cornice: false,
    type: 'standart',
  });

  const setBase = (price) => {
    setValues({ ...values, base: price });
  };

  const setWidth = (width) => {
    setValues({ ...values, width });
  };

  const setHeight = (height) => {
    setValues({ ...values, height });
  };

  const setCornice = (event) => {
    setValues({ ...values, cornice: +event.target.checked });
  };

  const setType = (type) => {
    setValues({ ...values, type });
  };

  const { base, width, height, cornice, type } = values;

  const materialCost = +(base * width).toFixed(2);
  const sewingCost = +(base * width * height).toFixed(2);
  const tapeCost = +(width * (height / TAPE_COEF) * TAPE_PRICE).toFixed(2);
  const corniceCost = cornice ? +(width * CORNICE_PRICE).toFixed(2) : 0;

  const totalPrice = +(materialCost + sewingCost + tapeCost + corniceCost).toFixed(2);

  return (
    <div>
      <RadioGroup>
        <RadioLabel>
          <RadioBtn checked={type === 'standart'}>
            <input
              type="radio"
              value="standart"
              checked={type === 'standart'}
              onChange={() => setType('standart')}
            />
          </RadioBtn>
          
        </RadioLabel>
        <RadioLabel>
          <RadioBtn checked={type === 'order'}>
            <input
              type="radio"
              value="order"
              checked={type === 'order'}
              onChange={() => setType('order')}
            />
          </RadioBtn>
           
        </RadioLabel>
      </RadioGroup>
      <Head>
        1.  <i></i>
      </Head>
      <SelectBase>
        {romeOptions.map((item) => (
          <Fabric
            key={item.id}
            url={item.image}
            onClick={() => setBase(item.price)}
            active={values.base === item.price}
          >
            {item.title}
            <Cost><br />{item.price} / </Cost>
          </Fabric>
        ))}
      </SelectBase>
      {base ? (
        <>
          <Head>
            2.  <i></i>
          </Head>
          <FormGroup>
            <Label>  ( )</Label>
            <br />
            <Input changed={setWidth} placeholder="   " />
          </FormGroup>
          <FormGroup>
            <Label> </Label>
            <br />
            <Input changed={setHeight} placeholder="   " />
          </FormGroup>
          <Checkbox onChange={setCornice} checked={cornice} title=" " />
          {width && height ? (
            <Result>
              <Calculation>
                    :
                <div> : {base} * {width} = {materialCost}</div>
                <div> : {base} * {width} * {height} = {sewingCost}</div>
                <div>
                    :&nbsp;
                  {width} * ({height}/{TAPE_COEF}) * {TAPE_PRICE} = {tapeCost}
                </div>
                {cornice ? (
                  <div> : {width} * {CORNICE_PRICE} = {corniceCost}</div>
                ) : null}
              </Calculation>
              <div>
                : {totalPrice} 
                <br />
                <Button onClick={() => toggleModal(true)}> </Button>
              </div>
            </Result>
          ) : null}
          {showOrderModal ? (
            <OrderModal
              details={` , ${baseMap[base]}, ${width}/${height}  ${cornice ? '+ ' : ''}.`}
              price={totalPrice}
              close={() => toggleModal(false)}
            />
          ) : null}
        </>
      ) : null}
    </div>
  );
};

export default Rome;
